@import url(https://fonts.googleapis.com/css2?family=Sulphur+Point:wght@300;400;700&display=swap);
body, html {
  padding: 0;
  margin: 0;
  --color-primary: #00bcd4;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Sulphur Point', sans-serif;

}

*::selection {
  background: #f76a8c;
  color: #fff;
}


* {
    box-sizing: border-box;
    overflow-x: hidden;
    list-style: none;
    margin: 0 ;
    padding: 0;
}



.App {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* justify-content: center; */
    margin-top: 10rem;
}

.file__select__container, .file__select__lable,  .preview {
    display: flex;
    justify-content: center;
  }
  
  .App {
    min-height: 100vh;
    width: 100vw;
    align-items: center;
  }
  
  .file__select__container {
    border: 3px dashed var(--color-primary);
    height: 300px;
    width: 60vw;
    align-items: flex-end;
    padding: 20px;
  }
  
  .drop__message {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .upload__icon {
    height: 70px;
    width: 70px;
  }
  
  .upload__icon g {
    fill: var(--color-primary);
  }
  
  .browse__file__message {
    font-size: 1.5rem;
    margin: 1rem;
  }
  
  .select__btn {
    height: 3rem;
    width: 13rem;
    background: var(--color-primary);
    color: #fff;
    font-size: 1.5rem;
    border: none;
    cursor: pointer;
  }

  
  .preview {
      margin-bottom: 1.5rem;
  }
  
  .img__container {
      height: 179px;
      width: 107px;
      text-align: center;
      margin: 5px;
      padding: 5px 0 0 0;
      overflow: hidden;
      border: 1px solid var(--color-primary);
  }

  .selected__image {
    height: 120px;
    width: calc(100% - 6px);
    margin: 0 3px;
    object-fit: contain;
    
}

.img__name {
    display: block;
    background: var(--color-primary);
    color: #fff; 
    width: 107px;
    padding: 0 5px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

h6 {
    margin: 0;
    font-size: 0.67em;
    line-height: 1.7;
}

.img__size {
    color: var(--color-primary);
}

.upload__btn {
    width: 60vw;
    height: 2.5rem;
    background: #3797a4;
    color: #fff;
    font-weight: 400;
    font-size: 1.2rem;
    margin: 2rem 0;
    border: #3797a4;
    cursor: pointer;
}

.progress__bar {
    background-color: #3797a4;
}



/* post__processing__container */

.post__processing__container {
    width: 60vw;
    margin: 5rem 0;
    border: 1px solid #e0e0e0;
    border-radius: 5px;
    background-color: #f9f9f9;
}

.heading {
    display: flex;
    flex-wrap: wrap;
    padding: .5rem 0;
    border-bottom: 1px solid #e0e0e0;
}


.row {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    height: 250px;
}

.row:not(:last-child) {
    border-bottom: 1px solid #e0e0e0;
}

.title__1, .img {
    flex: 40% 1;
    text-align: center;
}

.title__2, .title__3, .dimension, .action {
    flex: 30% 1;
    text-align: center;
}


.img {
    height: 200px;
    overflow: hidden;
}

.img>img {
    height: 200px;
    width: 100%;
    object-fit: contain;
}


* {
    text-decoration: none !important;
}

.nav {
    height: 5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 5rem;
    border-bottom: 1px solid #e0e0e0;
}

.logo li{
    font-size: 2rem;
    font-weight: 700;
    color: var(--color-primary);
}

.nav__items li{
    font-size: 1.4rem;
    font-weight: 400;
    color: var(--color-primary);

}

.crop__container {
    display: flex;
    width: 100vw;
    justify-content: space-around;
    padding:2rem;
}

.crop__img__container {

    width: 1024px;
    height: 600px;
    border: 1px solid var(--color-primary);
    overflow: hidden;
    object-fit: contain;
}


.preview__and__btn {
display: flex;
width: 400px;
height: 600px;
flex-direction: column;
align-items: center;
margin-left: 1rem;
}

.img__preview {
    height: 300px;
    width: 100%;
    border: 1px solid var(--color-primary);
    overflow: hidden;

}

.img__details {
    text-align: center;
}

.img__details h6 {
    margin: 0.5rem 0;
    font-size: 0.7rem;
}

.crop__and__submit {

    height: 4rem;
    width: 100%;
    border-radius: 5px;
    border: 1px solid var(--color-primary);
    background: var(--color-primary);
    color: #000000;
    font-size: 1.3rem;
    font-weight: 400;
    margin-top: 2rem;
}


.upload__all {
    width: calc(100vw - 4rem);
    margin: 2rem 2rem 5rem 2rem;
    height: 4rem;
    border-radius: 5px;
    border: 1px solid #3797a4;
    background: #3797a4;
    color: #ffffff;
    font-size: 1.3rem;
    font-weight: 400;
}
.album__container {
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem;
    margin-top: 2rem;

}

.album__container h2 {
    margin-bottom: 1rem;
    text-decoration: underline;
}


.album__card {
    width: calc(70% - 4rem);
    height: 250px;
    border: 1px solid #000000;
    border-radius: 5px;
    padding: 1rem;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 2rem 0;
}

.cropped_img1 {
    flex: 33% 1;
    /* width: 250px; */
    height: calc(250px - 2rem);   
}

.album__card img {
    width: 100%;
    height: 100%;
    object-fit: 100% 100%;
    padding: 0 2px;
    cursor: pointer;
}

.cropped_img2 {
    /* width: 150px; */
    flex: 15% 1;
    height: calc(250px - 2rem);  
}

.cropped_img3 {
    flex: 30% 1;
    /* width: 200px; */
    height: calc(250px - 2rem);  
}

.cropped_img4 {
    flex: 22% 1;
    /* width: 100px; */
    height: calc(250px - 2rem);  
}

